*,
*::after,
*::before {
    box-sizing: border-box;
}

html,
body,
.scroller {
    height: 100%;
}

.mp-pusher {
    position: relative;
    left: 0;
    height: 100%;
}

/* overlays for pusher and for level that gets covered */

#site-wrapper::after,
.mp-pusher::after,
.mp-level::after,
.mp-level::before {
    position: absolute;
    top: 0;
    right: 0;
    width: 0;
    height: 0;
    content: '';
    opacity: 0;
}

#site-wrapper.menu-open::after,
.mp-pusher::after,
.mp-level::after {
    z-index: 100;
    background: rgba(0, 0, 0, 0.3);
    -webkit-transition: opacity 0.3s, width 0.1s 0.3s, height 0.1s 0.3s;
    transition: opacity 0.3s, width 0.1s 0.3s, height 0.1s 0.3s;
}

#site-wrapper.menu-open::after,
.mp-pusher.mp-pushed::after,
.mp-level.mp-level-overlay::after {
    width: 100%;
    height: 100%;
    opacity: 1;
    -webkit-transition: opacity 0.3s;
    transition: opacity 0.3s;
}

.mp-pusher,
.mp-level {
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
}

/* --------------------------------------------
Mobile Styles
--------------------------------------------- */

.mp-menu {
    position: absolute; /* we can't use fixed here :( */
    top: 0;
    right: 100%;
    z-index: 1;
    width: 300px;
    height: 100vh;
    padding-top: 108px;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;

    clear: both;

    *zoom: 1;
}

.mp-menu:before {

    /* we can't use fixed here :( */
    content: '';
    display: table;
}

.mp-menu:after {

    /* we can't use fixed here :( */
    content: '';
    display: table;
    clear: both;
}

.mp-menu .icon-back {
    opacity: 0;
    -webkit-transition: opacity 0.5s;
    transition: opacity 0.5s;
    padding-top: 15px;
    position: absolute;
    right: 0;
    top: 0;
    width: 40px;
    z-index: 3;
    color: #b3559f;
    text-align: center;
    font-family: 'Open sans';
    font-size: 10px;
    font-weight: bold;
}

.mp-menu .icon-back::before {
    display: block;
    content: ' ';
    width: 0;
    height: 0;
    margin: 0 auto;
    margin-bottom: 3px;
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent;
    border-right: 10px solid #b3559f;
}

.mp-menu::before,
    .mp-menu::after {
    content: '';
    clear: both;
    display: block;
}

.mp-menu a {
    font-family: 'Open Sans';
}

.mp-menu .icon::before {
    font-size: 42px;
    float: left;
    margin-right: 3px;
}

.mp-menu nav {
    position: relative;
    height: 100%;
}

.mp-menu .main-title {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    background: $white;
    padding: 10px;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    height: 110px;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
    -webkit-box-align: center;
    -webkit-align-items: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
        -ms-flex-pack: center;
            justify-content: center;
}

.mp-menu .main-title img {
    width: 175px;
}

.mp-menu .main-title .icon-exit-circle::before {
    font-size: 32px;
    margin: 20px;
    color: $black;
    margin-left: 50px;
    margin-right: 0;
}

.mp-menu .title + a {
    display: none;
}

.mp-menu a.overview {
    display: none;
}

.mp-menu .mp-level-open {
    overflow-x: visible;
}

.mp-menu [data-level='2'] {
    overflow-y: scroll;
}

.mp-menu .mp-level-overlay > .icon-back {
    opacity: 1;
}

.mp-menu .mp-level-overlay > ul > li > a {
    border: none;
}

.mp-menu .mp-level-overlay > ul > li > a:hover {
    background: none;
}

.mp-level {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: $purple_light;
}

.mp-level .mp-level {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
}

.mp-level::after {
    z-index: -1;
}

.mp-level.mp-level-overlay {
    cursor: pointer;
}

@media (min-width: $(desktop)) {

    .mp-menu .our-neighborhoods li:first-child {
        display: none;
    }
}

.mp-menu .our-neighborhoods li > a {
    line-height: 25px;
}

.mp-level.mp-level-overlay.mp-level::before {
    width: 100%;
    height: 100%;
    background: transparent;
    opacity: 1;
}

/* overlap */

.mp-overlap .mp-level.mp-level-open {
    box-shadow: 1px 0 2px rgba(0, 0, 0, 0.2);
    -webkit-transform: translate3d(-40px, 0, 0);
            transform: translate3d(-40px, 0, 0);
}

/* First level */

.mp-menu > .mp-level,
.mp-menu > .mp-level.mp-level-open,
.mp-menu.mp-overlap > nav > .mp-level,
.mp-menu.mp-overlap > nav > .mp-level.mp-level-open {
    box-shadow: none;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}

/* cover */

.mp-cover .mp-level.mp-level-open {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}

.mp-cover .mp-level.mp-level-open > ul > li > .mp-level:not(.mp-level-open) {
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
}

/* content style */

.mp-menu ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

.mp-menu h2 {
    margin: 0;
    color: rgba(0, 0, 0, 0.4);
    text-shadow: 0 0 1px rgba(0, 0, 0, 0.1);
    font-weight: 300;
    font-size: 20px;
    color: $white;
    font-weight: bold;
    line-height: 45px;
    padding: 0.7em 1em 0.7em 1.8em;
}

.mp-menu.mp-overlap h2::before {
    float: left;
}

.mp-menu.mp-overlap h2 {
    text-align: left;
}

.mp-menu.mp-cover h2 {
    text-transform: uppercase;
    font-weight: 700;
    letter-spacing: 1px;
    font-size: 1em;
}

.mp-overlap .mp-level.mp-level-overlay > h2::before {
    opacity: 1;
    -webkit-transition: opacity 0.3s, -webkit-transform 0.3s;
    transition: opacity 0.3s, -webkit-transform 0.3s;
    transition: transform 0.3s, opacity 0.3s;
    transition: transform 0.3s, opacity 0.3s, -webkit-transform 0.3s;
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.mp-menu .container {
    display: block;
}

.mp-menu ul li > a {
    display: block;
    line-height: 45px;
    padding: 0.7em 1em 0.7em 1.8em;
    outline: none;
    box-shadow: none;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    border-top: 1px solid rgba(255, 255, 255, 0.1);
    box-sizing: border-box;
    font-size: 18px;
    color: $white;
    font-weight: bold;
    text-decoration: none;
    -webkit-transition: background 0.3s, box-shadow 0.3s;
    transition: background 0.3s, box-shadow 0.3s;
}

.mp-menu ul li > a::before {
    margin-right: 5px;
}

.mp-menu ul li > a span {
    display: block;
    font-weight: normal;
}

.mp-menu ul li::before {
    position: absolute;
    left: 10px;
    z-index: -1;
    color: rgba(0, 0, 0, 0.2);
    line-height: 3.5;
}

.mp-level .container li {
    width: 100%;
}

.mp-menu ul li a:hover,
.mp-level > ul > li:first-child > a:hover {
    background: rgba(0, 0, 0, 0.2);
    box-shadow: inset 0 -1px rgba(0, 0, 0, 0);
}

.mp-level > ul > li:first-child > a:hover,
.mp-level.mp-level-overlay > ul > li:first-child > a {
    box-shadow:
        inset 0 -1px rgba(0, 0, 0, 0),
        inset 0 1px rgba(0, 0, 0, 0);
}

/* seems like Chrome 34.0.1847.131 needs the second shadow otherwise the transition breaks */

.mp-back {
    background: rgba(0, 0, 0, 0.1);
    outline: none;
    color: #fff;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-weight: 700;
    display: block;
    font-size: 0.8em;
    padding: 1em;
    position: relative;
    box-shadow: inset 0 1px rgba(0, 0, 0, 0.1);
    -webkit-transition: background 0.3s;
    transition: background 0.3s;
}

.mp-back::after {
    font-family: 'linecons';
    position: absolute;
    content: '\e037';
    right: 10px;
    font-size: 1.3em;
    color: rgba(0, 0, 0, 0.3);
}

.mp-menu .mp-level.mp-level-overlay > .mp-back,
.mp-menu .mp-level.mp-level-overlay > .mp-back::after {
    background: transparent;
    box-shadow: none;
    color: transparent;
}

/* Fallback example for browsers that don't support 3D transforms (and no JS fallback) */

/* We'll show the first level only */

.no-csstransforms3d .mp-pusher,
.no-js .mp-pusher {
    padding-left: 300px;
}

.no-csstransforms3d .mp-menu .mp-level,
.no-js .mp-menu .mp-level {
    display: none;
}

.no-csstransforms3d .mp-menu > .mp-level,
.no-js .mp-menu > .mp-level {
    display: block;
}

