        color: #2c657f;
        -webkit-transition: all 0.5s;
        transition: all 0.5s;
    }

    #mp-menu .mp-level a::before {
        height: calc(100% - 14px);
        left: 0;
        top: 7px;
        width: 5px;
        opacity: 0;
        background: #2c657f;
    }

    #mp-menu .mp-level a:hover::before {
        opacity: 1;
    }

    #mp-menu .mp-level > a {
        display: none;
    }

    #mp-menu .our-neighborhoods .title, #mp-menu .our-homes .title, #mp-menu .lifestyle .title, #mp-menu .build-with-us .title, #mp-menu .about-us .title, #mp-menu .contact-us .title {
        margin-bottom: 10px;
    }

    #mp-menu .our-neighborhoods h2, #mp-menu .our-homes h2, #mp-menu .lifestyle h2, #mp-menu .build-with-us h2, #mp-menu .about-us h2, #mp-menu .contact-us h2 {
        font-size: 22px;
    }

    #mp-menu .our-neighborhoods .title {
        border-color: $blue;
    }

    #mp-menu .our-neighborhoods h2 {
        color: $blue;
        display: inline-block;
    }

    #mp-menu .our-homes .title {
        border-color: $purple;
    }

    #mp-menu .our-homes h2 {
        color: $purple;
    }

    #mp-menu .about-us .title {
        border-color: $purple;
    }

    #mp-menu .about-us h2 {
        color: $purple;
    }

    #mp-menu .build-with-us .title {
        border-color: $gold;
    }

    #mp-menu .build-with-us h2 {
        color: $gold;
    }

    #mp-menu .lifestyle .title {
        border-color: $blue;
    }

    #mp-menu .lifestyle h2 {
        color: $blue;
    }

    #mp-menu .contact-us .title {
        border-color: $green;
    }

    #mp-menu .contact-us h2 {
        color: $green;
    }
}
